export default {
    name: "Item",
    props: {
        value: {},
        visibleItems: {
            default: () => []
        },
        isFull: {
            type: Boolean,
            default: false
        }
    },
    computed: {
        taxPrimaries() {
            if (this.visibleItems.includes['hashtag']) {
                return this.value['terms'].filter(x => x.taxonomy === 'object');
            }
            return []
        }
    }
}
